<template>
  <div id='main-page' v-bind:style='{ backgroundImage: "url("+bgUrl+")" }'>
    <div id='beforelogin' v-if='init' v-show='!loggedIn'>
    </div>
    <div id='afterlogin' v-if='init' v-show='loggedIn'>
      <h1> <i> Welcome to Duke University! </i> </h1>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import {ui, uiConfig, firebase} from '../firebase.js'
import * as firebaseui from 'firebaseui'

const bgUrls = ['@/assets/Duke_01.png', '@/assets/Duke_02.jpeg', '@/assets/Duke_03.jpg',
		'@/assets/Duke_04.jpg', '@/assets/Duke_05.jpg', '@/assets/Duke_06.jpg']

export default {
  created() {
    this.bgUrl = bgUrls[Math.floor(Math.random() * (bgUrls.length-1))]
    console.log(this.bgUrl)
  },
  mounted() {
    firebase.auth().onAuthStateChanged( this.userChangeHandler )
    Vue.set(this, 'init', true)
  },
  data() {
    return {
      user: undefined,
      init: false,
      bgUrl: ''
    }
  },
  computed: {
    loggedIn: function() {
      return this.user != null
    }
  },
  methods: {
    userChangeHandler: function(user) {
      this.user = user
      ui.start('#beforelogin', uiConfig)
    }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Cinzel');
@import url('https://fonts.googleapis.com/css?family=PT+Sans');

#main-page {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.8;
  width: 100%;
  height: 100%;
}

#beforelogin {
  position: relative;
  width: 70%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  text-align: center;
  color: black;
  padding: 2% 5%;
  background: rgba(255,255,255,0.4);
  font-family: 'Cinzel', serif;
}

#afterlogin {
  position: relative;
  width: 70%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  text-align: center;
  color: black;
  padding: 2% 5%;
  background: rgba(255,255,255,0.4);
  font-family: 'Cinzel', serif;
}


#beforelogin input {
  margin-left: 5px;
  font-family: 'PT Sans', sans-serif;
}
</style>



